/* Styles go here */

.token {
  height: 150px;
  width: 150px;
  border-radius: 50%;
  display: inline-block;
}

.background{
  background-image: url(/DogeVilleDashboard/static/media/Landscape2.a87216b5.jpg);
  padding: 0px 0 0px 0;
  height: 100vh;
  background-size:100%;
  background-repeat: no-repeat;
}

.jumbotron {
  background: url(/DogeVilleDashboard/static/media/Landscape2.a87216b5.jpg) no-repeat center center fixed;
   background-size: cover;
   -o-background-size: cover;
}

.LeftText{
  padding: 50px 0 50px 0;
  color: red;
  text-align: left;
}

.RightText{
  padding: 50px 0 50px 0;
  color: red;
  text-align: Right;
}

